import { Mark } from '@ali/4ever-cangjie';
import type { UnderlineData } from '@ali/4ever-utils';

export type { UnderlineData };

interface Underline extends Mark<UnderlineData> {}

const Underline = {
  TYPE: 'underline',

  create(underline: string = 'single', color?: string): Underline {
    const data = { 
      value: underline,
    };
    if (color) {
      // @ts-ignore
      data.color = color;
    }
    return Mark.create<UnderlineData>({
      type: Underline.TYPE,
      data,
    });
  },

  isUnderline(maybeUnderline: any): maybeUnderline is Underline {
    return Mark.isMark(maybeUnderline) && maybeUnderline.type === Underline.TYPE;
  },
};

export default Underline;
